@import '../common/styles/sass/variables';

*,
*::after,
*::before {
    box-sizing: border-box;
}

.footer {
    height: 60vh;
    background-color: $bgSecondColor;
    .footerContainer {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: space-around;
        .infoBox {
            margin-top: 80px;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            gap: 10px;
            .form{
                //border: 2px solid #c79864;
                background-color: $bgColor;
                min-width: 300px;
                min-height: 150px;
                .iconBlock {
                    text-align: center;
                    margin-top: 15px;
                    .infoIcons {
                        color: white;
                        font-size: 30px;
                    }
                }

              //  margin-bottom: 20px;
                .mainText{
                    text-align: center;
                    font-family: $TitleFontFamily;
                    font-size: 20px;
                    color: #000;
                    margin-bottom: 5px;
                    font-weight: 600;
                    line-height: 1.2;
                    text-transform: capitalize;
                }
                .paragraph{
                    font-family: $TitleFontFamily;
                    font-size: 16px;
                    margin: 0;
                    text-align: center!important;
                    color: #6c757d;
                    font-weight: 400;
                    line-height: 1.6;
                }
                &:hover {
                    border: 2px solid #c79864;
                }
            }
        }
        .title {
            background-color: powderblue;
        }
        .wrapper {
            display: flex;
            flex-direction: column;
            width: 100%;
            align-items: center;
            .socialMenu {
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                .icon {
                    height: 60px;
                    width: 60px;
                    //border: 1px solid ;
                    text-align: center;
                    margin-inline: 2%;
                    // background-color: #b4c2d9;
                    .iconStyle {
                        color: $primaryColor;
                        font-size: 35px;
                    }
                }
            }

            .copyWriter {
                color: white;
                font-family: $TitleFontFamily;
                padding: 10px;
            }
        }
    }
}


